import { useForm, Controller } from 'react-hook-form';
import {Button,Form} from "react-bootstrap";



export default function TodoItemsForm(props) {

    const state = props.state
    const setState = props.setState

    const { control, handleSubmit, reset, watch } = useForm();



    return (
        <Form
            onSubmit={handleSubmit((formData) => {

                let a={...state,
                    todoItems: [
                    { id: props.generateId(), done: false, ...formData},
                    ...state.todoItems,
                ]}
                setState(a)
                reset({ title: '', details: '' });
            })}
        >
            <Controller
                name="title"
                control={control}
                defaultValue=""
                rules={{ required: true }}
                render={({ field }) => (
                    <Form.Control className='mb-2' {...field} type="text" placeholder="Title" />
                )}
            />
            <Controller
                name="details"
                control={control}
                defaultValue=""
                render={({ field }) => (
                    <Form.Control className='mb-2' {...field} type="text" placeholder="Description" />
                )}
            />
            <Button
                className='mb-2'
                variant="primary"
                type="submit"
                disabled={!watch('title')}
            >
                Add
            </Button>
        </Form>
    );
}
