import React, {useEffect, useState} from 'react';
import {Modal,Button} from "react-bootstrap";
import TodoItemsForm from "./TodoItemsForm";
import {TodoItemsList} from "./TodoItems";


const TodoList = (props) => {


    const [state,setState]=useState({todoItems:[]})
    const localStorageKey = 'todoListState';


    function generateId() {
        return `${Date.now().toString(36)}-${Math.floor(
            Math.random() * 1e16,
        ).toString(36)}`;
    }

    useEffect(() => {

        const savedState = localStorage.getItem(localStorageKey);

        if (savedState) {
            try {
               setState( JSON.parse(savedState)) ;
            } catch {}
        }
/*
        setState(savedState)
*/
    },[]);

    useEffect(() => {
        localStorage.setItem(localStorageKey, JSON.stringify(state));
    }, [state]);

    return (
        <Modal show={props.show} onHide={props.handleClose}>
            <Modal.Header closeButton>
                <Modal.Title> Todo List</Modal.Title>
            </Modal.Header>
            <Modal.Body><TodoItemsForm state={state}
                                       generateId={generateId}
                                       setState={setState}/>

                <TodoItemsList state={state}
                               generateId={generateId}
                               setState={setState}/></Modal.Body>
            <Modal.Footer>
                <Button variant="secondary" onClick={props.handleClose}>
                    Close
                </Button>
            </Modal.Footer>
        </Modal>
    );
};

export default TodoList;
